export default {
  data() {
    return {
      products: [],  // 用來儲存JSON資料
      category: ['全部商品', '特色推薦', '經典設計', '木椅', '塑膠椅', '金屬椅', '沙發'],
      currentCategory: '全部商品',
    }
  },
  methods: {
    getProducts() {
      const vm = this;
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products/all`;  // 取得全部商品列表
      
      vm.$http.get(api).then((response) => {
        console.log(response.data);
        vm.products = response.data.products;

      });
    },
    getCategory() {
      const vm = this;

      const getCategoryApiPath = `${process.env.BACKEND_API}/product/category`;
      console.log(getCategoryApiPath);
      vm.$http.get(getCategoryApiPath).then((response) => {
        console.log(response.data);
        
        // vm.products = response.data.products;
      });
    }
  },
  created() {
    this.getCategory();
    this.getProducts();
    
  },

  
}
</script>

